	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"You and Your Research is the blueprint for a successful career in any discipline, not just research; in fact, the talk has the nickname “You and Your Career”. In this lecture, Richard Hamming shares his observations on “why do so few scientists make significant contributions and so many are forgotten in the long run?’’ Some of the key ideas include courage, luck, drive (“knowledge and productivity are like compound interest”), a focus on important problems (“If you do not work on an important problem, it’s unlikely you’ll do important work”), open doors, selling the work (“I suggest that when you open a journal, as you turn the pages, you ask why you read some articles and not others”), and much more. This should be required viewing for every high school student.","meta":{"duration":"PT44M3S"},"presenter":{"name":"Richard Hamming"},"source":{"id":"a1zDuOPkMSw","provider":"youtube"},"slug":"you-and-your-research","tag":["Inspiring","Career","Educational"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/a1zDuOPkMSw/default.jpg","height":90,"width":120}},"title":"You and Your Research"}}]}},"pathContext":{"speaker":"Richard Hamming"}}

